<template>
  <div>
    <flexbox class='box' orient="vertical">
      <flexbox-item>
        <h3>{{content.title}}</h3>
        <p class="author">作者：{{content.author.loginname}}</p>
        <p class="content">{{{content.content}}}</p>
      </flexbox-item>
    </flexbox>
  </div>
</template>

<script>
import { Flexbox, FlexboxItem } from 'vux'

export default {
  route: {
    data (transition) {
      
      this.$http.get(`https://cnodejs.org/api/v1/topic/${this.$route.params.userId}`)
        .then((data) => {
          this.content = data.data.data
        })
    }
  },
  components: {
    Flexbox,
    FlexboxItem
  },
  data () {
    return {
      content: ''
    }
  }
}
</script>

<style scoped lang='scss'>
@import '~vux/vux.css';
.box {
  margin-top: 5%;
  h3 {
    text-align: center;
    font-size: 24px;
  }
  .author {
    color: #C3BED4;
    font-size: 17px;
    margin-bottom: 5%;
    margin-left: 5%;
  }
  .content {
    padding: 5%;
  }
}
</style>


